/* aboutPage.css */

body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
  color: #333;
}

.about-container {
  max-width: 800px;
  margin: 50px auto;
  padding: 40px;
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  transition: transform 0.3s;
}

.about-container:hover {
  transform: scale(1.02);
}

.about-container h2 {
  font-size: 36px;
  margin-bottom: 20px;
  color: #2c3e50;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.about-container p {
  margin-bottom: 20px;
  font-size: 18px;
  line-height: 1.8;
  color: #7f8c8d;
}

.about-container p:last-child {
  margin-bottom: 0;
}

.menu-container {
  display: flex;
  justify-content: center;
  margin-top: 40px;
}

.menu-container a {
  margin: 0 10px;
  padding: 12px 24px;
  background-color: #2ecc71;
  color: #fff;
  text-decoration: none;
  border-radius: 24px;
  transition: background-color 0.3s ease, transform 0.3s ease;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.menu-container a:hover {
  background-color: #27ae60;
  transform: translateY(-3px);
}

@media (max-width: 600px) {
  .about-container {
    padding: 20px;
  }

  .about-container h2 {
    font-size: 28px;
  }

  .about-container p {
    font-size: 16px;
  }

  .menu-container a {
    margin: 5px;
    padding: 10px 20px;
  }
}
