/* homePage.css */

body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #f4f4f4;
}

header {
  background-color: #333;
  color: white;
  padding: 10px 0;
  text-align: center;
}

header h1 {
  margin: 0;
  font-size: 2.5em;
}

nav {
  margin-top: 10px;
}

nav ul {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

nav ul li {
  margin: 0 15px;
}

.nav-link {
  color: white;
  text-decoration: none;
  font-size: 1.2em;
  padding: 10px 20px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.nav-link:hover {
  background-color: #575757;
}

.right {
  margin-left: auto;
}

.banner {
  background: url('./image/loc.jpg') no-repeat center center/cover;
  color: white;
  text-align: center;
  padding: 100px 20px;
}

.banner h2 {
  font-size: 3em;
  margin: 0;
}

.banner p {
  font-size: 1.5em;
  margin: 20px 0;
}

.btn {
  background-color: #ff6600;
  color: white;
  padding: 15px 30px;
  text-decoration: none;
  font-size: 1.2em;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.btn:hover {
  background-color: #e65c00;
}

.services {
  background-color: white;
  padding: 50px 20px;
  text-align: center;
}

.services h3 {
  font-size: 2.5em;
  margin-bottom: 30px;
}

.service {
  display: inline-block;
  width: 300px;
  margin: 20px;
  padding: 20px;
  background-color: #f9f9f9;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
}

.service img {
  width: 100%;
  border-radius: 10px;
}

.service h4 {
  font-size: 1.5em;
  margin: 20px 0 10px;
}

.service p {
  font-size: 1em;
  color: #555;
}

footer {
  background-color: #333;
  color: white;
  text-align: center;
  padding: 20px 0;
  position: relative;
  bottom: 0;
  width: 100%;
}
